import React, { useState, useEffect } from "react";
import $ from "jquery";
import "./App.css";
import Search from "./search.js";
import Gallery from "./gallery/gallery.js";
import Recipe from "./gallery/recipe.js";
import Account from "./account/account.js";
import Create from "./create/create.js";

const App = () => {
  const [show, setShow] = useState(false);
  const [view, setView] = useState("gallery");
  const [recipes, setRecipes] = useState([]);
  const [searchedRecipes, setSearchedRecipes] = useState([]);
  // const [searchTerm, setSearchTerm] = useState('');
  const location = "app";

  function getRecipes() {
    $.ajax({
      url: "/api/recipes",
      type: "GET",
    })
      .then((response) => {
        const { recipes } = response;
        setRecipes([...recipes]);
        // setSearchedRecipes([...recipes])
      })
      .then(() => {
        setShow({ show: true });
      });
  }

  function postCollect(data) {
    $.ajax({
      url: "https://foodys-api.herokuapp.com/api/collects",
      type: "POST",
      data: data,
      success: (e) => console.log(e),
      error: (e) => console.log(e),
    }).done(function () {
      console.log("Submitted");
    });
  }

  useEffect(() => {
    getRecipes();
    postCollect({ location: location });
  }, []);

  function postRecipe(recipe) {
    $.ajax({
      url: "/api/recipes",
      type: "POST",
      data: recipe,
      success: (e) => console.log(e),
      error: (e) => console.log(e),
    }).done(function () {
      console.log("Submitted");
    });
  }

  function changeView(option) {
    setView(option);
  }

  function searchRecipes(term) {
    // console.log(term)
    var recipes = this.state.recipes;
    var newSearched = [];
    for (var i = 0; i < recipes.length; i++) {
      if (term === "") {
        setSearchedRecipes({ searchedRecipes: recipes });
      }
      if (recipes[i].name.toLowerCase().includes(term.toLowerCase())) {
        newSearched.push(recipes[i]);
        // var addRecipe = recipes[i];
      }

      setSearchedRecipes({ searchedRecipes: newSearched });
    }
  }

  function renderView() {
    if (view === "gallery") {
      return (
        <Gallery
          handleClick={() => {
            changeView("recipeview");
          }}
          recipes={recipes}
          searchedRecipes={searchedRecipes}
        />
      );
    } else if (view === "account") {
      return (
        <Account
          handleClick={() => {
            changeView("recipeview");
          }}
          recipes={recipes}
          searchedRecipes={searchedRecipes}
        />
      );
    } else if (view === "create") {
      return <Create recipes={recipes} postRecipe={postRecipe} />;
    } else {
      return (
        <Recipe
          handleClick={() => {
            changeView("account");
          }}
          recipes={recipes}
          searchedRecipes={searchedRecipes}
        />
      );
    }
  }

  if (show === false) {
    return <div className="loading">Loading ...</div>;
  }

  return (
    <div>
      <div className="nav">
        <span
          className="logo"
          onClick={() => {
            getRecipes();
            changeView("gallery");
          }}
        >
          Foodys
        </span>

        <div className="search-bar">
          <Search searchRecipes={searchRecipes} />
        </div>

        <div className="nav-sub">
          <span
            className={view === "gallery" ? "nav-selected" : "nav-unselected"}
            onClick={() => {
              getRecipes();
              changeView("gallery");
            }}
          >
            See all Recipes
          </span>

          <span
            className={view === "create" ? "nav-selected" : "nav-unselected"}
            onClick={() => changeView("create")}
          >
            Add a Recipe
          </span>

          <span
            className={view === "account" ? "nav-selected" : "nav-unselected"}
            onClick={() => changeView("account")}
          >
            <div className="card-profile">
              <img
                alt=""
                src="https://www.the-happy-chef.com/assets/img/team/team-3.jpg"
                className="profile-nav"
              />
            </div>
          </span>
        </div>
      </div>

      <div className="main">{renderView()}</div>

      <div className="footer">
        <span>@ 2021 foodys</span>
      </div>
    </div>
  );
};

export default App;
