/* eslint-disable array-callback-return */
// eslint-disable-next-line no-unused-vars
import React, { useState, useContext } from "react";
import Review from "./review.js";
import { HealthContext } from "../../context/healthContext.js";

const Recipe = (props) => {
  const { recipeStore } = useContext(HealthContext);
  // eslint-disable-next-line no-unused-vars
  const [recipe, setRecipe] = recipeStore;
  console.log("recipe", recipe);

  return (
    <div className="post">
      <div className="intro">
        <img
          alt=""
          className="post-image"
          src={recipe.imageURL}
          onClick={(e) => {
            this.props.handleClick();
          }}
        />
        <h2>
          <b>{recipe.name}</b>
        </h2>
        <p>like: {recipe.like}</p>
        <br></br>
        <img
          alt=""
          className="post-user"
          src={recipe.userURL}
          onClick={(e) => {
            this.props.handleClick();
          }}
        />
        <p>{recipe.user}</p>
      </div>

      <div className="ingredients">
        <br></br>
        <h3>
          <b>Ingredients</b>
        </h3>
        {recipe.ingredients.map((item, i) => {
          return <p key={i}>{item}</p>;
        })}
      </div>

      <div className="method">
        <br></br>
        <h3>
          <b>Method</b>
        </h3>
        <br></br>
        {recipe.methods.map((item, i) => {
          if (item) {
            return (
              <div key={i}>
                <h4>
                  <b>Step: {i}</b>
                </h4>
                <img alt="" src={item.imageURL} />
                <p>{item.description}</p>
                <br></br>
              </div>
            );
          }
        })}
        <br></br>
        <Review />
        <br></br>
      </div>
    </div>
  );
};

export default Recipe;
