import React, { useContext } from "react";
import { HealthContext } from "../../context/healthContext.js";

const Card = (props) => {
  const { recipeStore } = useContext(HealthContext);
  // eslint-disable-next-line no-unused-vars
  const [recipe, setRecipe] = recipeStore;
  const { item } = props;
  const { imageURL, name, user, userURL, like } = item;
  return (
    <div className="card">
      <img
        alt=""
        src={imageURL}
        className="card-photo"
        onClick={(e) => {
          props.handleClick();
          setRecipe(item);
        }}
      />
      <div className="container">
        <h4>
          <b>{name}</b>
        </h4>
        <div>like:{like}</div>
        <div className="card-profile">
          <img alt="" src={userURL} className="profile-card" />
          <p>{user}</p>
        </div>
      </div>
    </div>
  );
};

export default Card;
